import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import ListSubheader from '@material-ui/core/ListSubheader';
import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/Info';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Rating from '@material-ui/lab/Rating';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    marginTop: "3%",
    marginLeft: "10%",
    marginRight: "10%",
    position: "absolute",
  },
  gridList: {
    //width: "100%",
    height: "90%",
    padding: "20px 20px"
  },
  griditem:{
    //padding: "20px 20px"
  },
  icon: {
    color: 'rgba(255, 255, 255, 0.54)',
  },
  card:{
    width:"80%"
  },
  media: {
    height: 130,
  },
  
}));


export default function TitlebarGridList() {
  const classes = useStyles();
  const [trails,setTrails]= useState({resultList:[]});

 function getTrail(){
   var temp;
 
   var headers = new Headers();
       headers.append('Accept', 'application/json');
       headers.append('Content-Type', 'application/json');
 
   fetch("https://trailobyte-275015.ew.r.appspot.com/rest/query/byUser",{
       method: 'POST',
       headers: headers,
       body: JSON.stringify({
         param: localStorage.getItem("username"),
         pageSize: 6,
         cursor: null,
     })
   }).then( async (response) => {
       temp = await response.json();
       setTrails(temp);
       localStorage.setItem("cursortrailuser",temp.cursor);
   })
 }

 useEffect(()=> {
   getTrail();
},[]);

  return (
    <div className={classes.root}>
      <GridList cellHeight={280} className={classes.gridList} spacing={20}>
        {trails.resultList.map((trail) => (
          <GridListTile key={trail.name}  className={classes.griditem}>
          <Card className={classes.card}>
            <CardMedia
              className={classes.media}
              image="/imagens/montanha.jpg"
              title={trail.name}
            />
            <CardContent>
              <Typography gutterBottom variant="h5" component="h2">
                {trail.name}
              </Typography>
              <Typography variant="body2" color="textSecondary" component="p">
                {trail.description}
              </Typography>
            </CardContent>
          <CardActions>
            <Button size="small" color="primary">
              Edit
            </Button>
            <Button size="small" color="primary">
              Details
            </Button>
            <Rating name="read-only" value={trail.avgRating} readOnly />
          </CardActions>
        </Card>
        </GridListTile>
        ))}
      </GridList>
    </div>
  );
}