import React from "react";
import './Inicial.css';
import { withRouter } from "react-router-dom";
import history from '../../history';
import Content from "../utils/Content";

class Inicial extends React.Component {

    gotoLogin() {
        history.push("/login");
    }

    gotoRegister() {
        history.push("/register");
    }

    render() {
       return (
            <div className="app">
                <header className="headerapp">
                    <div className="headerapp-text">
                    <a>Venha conhecer os nossos caminhos</a>
                    </div>
                </header>
                <div className="barapp">
                    <ul className="app">
                        <li className="liapp"><a className="aapp" href="/">Home</a></li>
                        <li className="liapp"><a className="aapp" href="login">Trail</a></li>
                        <li  style={{float: 'right'}}><a className="aapp" href="login">Login</a></li>
                        <li  style={{float: 'right'}}><a className="aapp" href="register">Sign up</a></li>
                        <li className="liapp"><a className="aapp" href="login">Quizz</a></li>
                    </ul>
                </div>
                <div className="contentapp">
                        <Content/>
                </div>
            </div>
        );
    }
}

export default withRouter(Inicial);