/*global google*/
import React, {Component} from 'react';
import { GoogleMap, withGoogleMap,withScriptjs,Marker,DirectionsRenderer} from "react-google-maps";
import { withRouter } from 'react-router-dom';
import _ from 'lodash';

const Map = withScriptjs(
    withGoogleMap(props => (
        <GoogleMap
        defaultZoom={10} 
        defaultCenter={{lat:38.7071, lng: -9.13549}}
        >
            {props.marks.map((mark, index) => (
                <Marker
                key={index}
                position={mark}

            />
            ))}
        
        {props.directions && <DirectionsRenderer directions={props.directions} />}
        </GoogleMap>
    ))
);

class MapTest6 extends Component{
    constructor(){
        super();
        this.state={
            directions: null,
            marks: [],
            marksname: [],
            marksdesc: [],
            marksstopat: [],
            name: null,
            description: null,
            start: null,
            end: null,
            trailImg: null,
            dist: null,
            avgRating: null,
            nRatings: null,
            verified: null,
        };   
    }
    

    getDirection = () => {
        const directionsService = new google.maps.DirectionsService();
        const markers = this.state.marks;
        const markersname = this.state.marksname;
        const markersdesc = this.state.marksdesc;
        const markersstopat = this.state.marksstopat;

        this.setState({directions: null});

        const origin = markers[0];
        const destination = markers[Object.keys(markers).length-1];
        const waypts = [];

        for( let i=1; i<Object.keys(markers).length-1;i++){
            let location = {
                location: markers[i],
                stopover: markersstopat[i],
            };
            waypts.push(location);
        }

        directionsService.route({
            origin: origin,
            destination: destination,
            travelMode: google.maps.TravelMode.DRIVING,
            waypoints: waypts,
          }, (result, status) => {
            if (status === google.maps.DirectionsStatus.OK) {
                this.setState({directions: result});
              let distance = _.flatMap(result.routes, route => _.flatMap(route.legs, leg => leg.distance.value));  
              let sum = _.sum(distance);
              sum = sum/1000;
              this.setState({dist: sum});
            } else {
              console.error(`error fetching directions ${result}`);
            }
          });
    }

    componentDidMount = () => {
        const { mapid } = this.props.match.params
        this.load(mapid);
    }


    load(name){
        var requestOptions = {
            method: 'GET',
        };

    fetch('https://trailobyte-275015.ew.r.appspot.com/rest/trail/OPT3OP/'+ name,requestOptions)
       .then(async (response) => {
           var temp = await response.json();
            this.setState({
                description: temp.description,
                start: temp.start,
                end: temp.end,
                trailImg: null,
                dist: temp.dist,
                avgRating: temp.avgRating,
                nRatings: temp.nRatings,
                verified: temp.verified,
            });

            var markers = temp.markers;
            for(let i = 0; i<Object.keys(markers).length-1;i++){
                this.setState({marks: [...this.state.marks, temp.markers[i].coords],
                    marksname: [...this.state.marksname, temp.markers[i].name],
                    marksdesc: [...this.state.marksdesc, temp.markers[i].content],
                    marksstopat: [...this.state.marksstopat, temp.markers[i].stopover]});
            }
            this.getDirection();
           })
       .catch(error => alert("Server not available"));
   }

    render(){
        return(
            <div style={{width:'100vw', height:'100vh'}}>
                <Map
                googleMapURL="https://maps.googleapis.com/maps/api/js?key=AIzaSyD2HdYk7gSqjAnTchqAL4EilOOBWLjPExA"
                loadingElement={<div style={{ height: `100%` }} />}
                containerElement={<div style={{ height: `100%` }} />}
                mapElement={<div style={{ height: `100%` }} />}
                marks={this.state.marks}
                directions={this.state.directions}
                >
                </Map>
            </div>
        );
    }
}

export default withRouter(MapTest6);